html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.img-logo {
  width: 40px;
  height: 40px;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  margin-left: 20px;
}

.nav_link {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav_link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav_item:first-child {
  margin-left: 0;
}
